
.gl-app(:class="{'hide-thumbnail': !thumbnail}"
        :style='{"background": urlThumbnail}' oncontextmenu="return false")

  .left-side
    collapsible-panel(v-if="!thumbnail" direction="left" :locked="true")
      .panel-items
        h3 {{ vizDetails.title }}
        p  {{ vizDetails.description }}

  trip-viz.anim(v-if="!thumbnail"
                :center="vizDetails.center"
                :colors="COLOR_OCCUPANCY"
                :drtRequests="$options.drtRequests"
                :dark="globalState.isDarkMode"
                :paths="$options.paths"
                :settingsShowLayers="SETTINGS"
                :searchEnabled="searchEnabled"
                :simulationTime="simulationTime"
                :traces="$options.traces"
                :vehicleLookup = "vehicleLookup"
                :onClick = "handleClick")

  zoom-buttons(v-if="!thumbnail")

  .right-side(v-if="isLoaded && !thumbnail")
    collapsible-panel(direction="right")
      .big.clock
        p {{ myState.clock }}

      .panel-items
        legend-colors.legend-block(title="Anfragen:" :items="legendRequests")

        legend-colors.legend-block(v-if="legendItems.length"
          title="Passagiere:" :items="legendItems")

        .search-panel
          p.speed-label(:style="{margin: '1rem 0 0 0'}") Suche:
          form(autocomplete="off")
          .field
            p.control.has-icons-left
              input.input.is-small(type="email" placeholder="Search..." v-model="searchTerm")
              span.icon.is-small.is-left
                i.fas.fa-search

        settings-panel.settings-area(:items="SETTINGS" @click="handleSettingChange")

        .speed-block
          p.speed-label Geschwindigkeit:
            br
            | {{ speed }}x

          vue-slider.speed-slider(v-model="speed"
            :data="speedStops"
            :duration="0"
            :dotSize="20"
            tooltip="active"
            tooltip-placement="bottom"
            :tooltip-formatter="val => val + 'x'"
          )

  playback-controls.bottom-area(v-if="!thumbnail && isLoaded"
      @click='toggleSimulation'
      @time='setTime'
      :timeStart = "timeStart"
      :timeEnd = "timeEnd"
      :isRunning = "myState.isRunning"
      :currentTime = "simulationTime")

